export const requestGet = (url, data) => {
    return fetch(url, {
        method: "Get",
        headers: { 'Content-Type': 'application/json' },
    });
};

export const requestPost = function requestPost(url, data) {
    return fetch(url, {
        method: "POST",
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data)
    });
};

export default {
    requestGet,
    requestPost
}


