import React, { Component } from "react";
import { Button, Badge } from "reactstrap";
import api from "../service/api"

class Login extends Component {
    state = {}
    constructor(props) {
        super(props);
        // create a ref to store the textInput DOM element
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleSubmit() {
        console.log(this.state);
        api.login({
            id: this.state.id,
            pwd: this.state.pwd
        }).then((res)=>{
            this.handleRedirect(res);
        });
    }

    handleRedirect(res) {
        console.log(res);
        console.log('redirect');
        if (res.status === 200) {
            window.location.href = '/';
            console.log('succ');
        } else {
            console.log('failed');
        }
    }

    render() {
        return (
            <div className={this.props.className}>
                <Badge color="primary">BW NEWSs</Badge>
                <br />
                <input placeholder="User name" onChange={e=>this.setState({id:e.target.value})} type="text"></input>
                <br />
                <input placeholder="Password" onChange={e=>this.setState({pwd:e.target.value})} value={this.state.pwd} type="text"></input>
                <br />
                <Button color="primary" onClick={this.handleSubmit}>Login</Button>
            </div>
        )
    }
}

export default Login;


